import {useForm} from 'react-hook-form'
import { ErrorMessage } from '@hookform/error-message'
import s from './login.module.css'
import { connect } from 'react-redux'
import { login } from '../../data/auth-reduser copy'
import { Navigate } from 'react-router-dom'



const Login= (props) => {
// useForm()   !!!        
const{register,
      handleSubmit, 
      formState:{errors},
      setError,
      watch
   }
      = useForm() 
// handleSubmit  data  onSubmit

const onSubmit = async (data) => { 
   let result = await props.login(data.login, data.password, data.checkBox, true)
   if (!result.success) {
      setError("root",{type:"server", message:result.error} )
   } 
}

console.log(watch("password")) 


if(props.isAuth){
   return <Navigate to="/profile" replace />
}

return (
   /* handleSubmit    'react-hook-form'     onSubmit, 
    ,         input    onSubmit,   
    data
   */
   <form onSubmit={handleSubmit(onSubmit)}>
   <div >
     <input className={errors.login ? s.inputError:s.input} placeholder='' {...register("login", { required: ' ' })} />
     <div>{errors.login ? errors.login.message:"\u00A0"}</div>  
   </div>


   <div>
      <div>
         <input className={errors.login ? s.inputError:s.input} placeholder='' {...register("password", { required: ' ' })} />
            {errors.password ? (
               <div>{errors.password.message}</div>
            ) : errors.root ? (
               <div>{errors.root.message}</div>
            ) : (
               <div>{"\u00A0"}</div>
            )}
       
      </div>
     

     {/* errors will return when field validation fails  */}
   </div>  

     <input type="checkbox" {...register("checkBox", )} />
      <div> 
         <input type="submit" />
      </div>
    
   </form>
 )

}
const mapStateToProps =(state)=> ({
   isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login}) (Login)























